import {mapGetters, mapState} from "vuex";

    export default {
        data() {
            return {
                list: [],
                loading: false,
                page: 2,
            }
        },
        computed: {
            ...mapState({
                couponImg: state => state.mallConfig.__wxapp_img.coupon,
            }),
            ...mapGetters({
                userInfo: 'user/info',
            })
        },
        methods: {
            getList() {
                let that = this;
                that.$request({
                    url: that.$api.coupon.list,
                    method: 'get',
                }).then(response => {
                    uni.hideLoading();
                    that.$hideLoading();
                    if (response.code === 0) {
                        that.list = response.data.list;
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(() => {
                    uni.hideLoading();
                    that.$hideLoading();
                });
            },
            getMore() {
                let that = this;
                uni.showLoading({
                    title: '加载中...'
                });
                that.$request({
                    url: that.$api.coupon.list,
                    data: {
                        page: that.page
                    },
                }).then(e => {
                    uni.hideLoading();
                    if (e.code == 0) {
                        if (e.data.list.length > 0) {
                            that.list = that.list.concat(e.data.list);
                            that.page++;
                        }
                    } else {
                        uni.showToast({
                            title: e.msg,
                            icon: 'none',
                            duration: 1000
                        });
                    }
                }).catch(e => {
                    uni.hideLoading();
                });
            },
            toDetail(id) {
                uni.navigateTo({
                    url: '/pages/coupon/details/details?id=' + id
                });
            },
            receive(coupon) {

                let that = this;
                if(that.loading) {
                    return false
                }
                that.loading = true;
                uni.showLoading({
                    title: '领取中...'
                });
                that.$request({
                    url: that.$api.coupon.receive,
                    data: {
                        coupon_id: coupon.id
                    },
                }).then(response => {
                    that.loading = false;
                    uni.hideLoading();
                    if (response.code === 0) {
                        setTimeout(function (row) {
                            that.$store.dispatch('page/actionSetCoupon', {
                                list: [Object.assign(coupon, response.data)],
                                type: 'receive'
                            });
                            that.getList();
                        }, 200)
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000
                        });
                        that.getList();
                    }
                }).catch(() => {
                    uni.hideLoading();
                    that.$event.on(that.$const.EVENT_USER_LOGIN).then(() => {
                        that.receive(coupon);
                    });
                });
            },
        },
        onLoad() { this.$commonLoad.onload();

        },
        onShow() {
            this.$showLoading({
                text: '加载中...'
            });
            this.getList();
        },
        filters: {
            keepNumbers(n) {
                return Number(n);
            }
        },
        onReachBottom() {
            this.getMore();
        },
    }
